.navbar{
    background-color: var(--bg-light);
    padding: 10px 0px;
    border-bottom: 5px solid var(--clr-border);
}
.navbar > ul{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
}
.navbar > ul > li{
    margin: 0px 10px;
}
.link-item{
    color: var(--clr-link);
    text-decoration: none;
    font-size: var(--fs-link);
}
.link-item:hover{
    color: var(--clr-hover);
}