*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;


} 
:root{
  --fs-link : 2.0rem;
  --clr-link: black;
  --clr-hover: rgb(99, 104, 109);
  --bg-light: rgb(246, 243, 243);
  --clr-border: black;
}

.app-content{
  max-width: 900px;
  margin: auto;
  margin-top: 10px;
  /* margin: 10px 20%; */
}
h1,h2{
  font-size: 2.3rem;
  margin: 5px 0px;
  padding: 0px;
}
h2{
  font-size: 1.8rem;
}
p{
  font-size: 1.3rem;
  margin-bottom: 1rem;
}
h3{
  font-size: 1.3rem;
}

button {
  padding: 10px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}
button:hover{
  background-color: rgba(0,0,0,0.85);
}
.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}
#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 320px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}
textarea{
  resize: vertical;
  min-height: 100px;
}
#add-comment-form button {
  width: 320px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.articlePage{
  display: flex;
}
.article{
  flex: 0.7;
}
.sideBar{
  flex: 0.3;
}